<template>     
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>15. 保險公司是否已知您有未據實告知之情事，如以電話告知您、寄發通知書告知解約、或保險業務員向您說明 ?</h3>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
             <div>
                <button @click="show = true" class="btn btn-outline-success" style="width:100%; padding: 10px 30px">
                    <b>是</b>
                </button>
            </div>
            <component1 v-if="show === true" ></component1>
            <br>
            <div>
                <button @click="press" class="btn btn-outline-danger" style="width:100%; padding: 10px 30px">
                    <b>否</b>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import component1 from "@/components/15-1.vue";
export default {
    data(){
        var today = new Date(new Date().setHours(0,0,0,0));
        var insurerDate = new Date(this.$route.params.insurerDate);
        insurerDate.setHours(0,0,0,0);
        var settlementDate = new Date(this.$route.params.settlementDate);
        settlementDate.setHours(0,0,0,0);
        var twoY = new Date(new Date(insurerDate).setFullYear(insurerDate.getFullYear()+2));
        return{
            today,
            insurerDate,
            settlementDate,
            twoY,
            show: false
        }
    },
    computed:{
        diff: function(){
            var day = 1000*60*60*24;
            return Math.abs(this.settlementDate.setHours(0,0,0,0) - this.today)/day;
        }
    },
    methods: {
        press: function(){
            if(this.twoY <= this.settlementDate){
                // alert("理賠事由之發生超過要保日期兩年內");
                this.$router.push({name: 'result3'});
            }
            else{
                this.$router.push({ 
                    name: '16',
                    params:{
                        name: this.$route.params.name,
                        type: this.$route.params.type,
                        samePeople: this.$route.params.samePeople,
                        insurerCareer: this.$route.params.insurerCareer,
                        insuredCareer: this.$route.params.insuredCareer,
                        insurerDate: this.$route.params.insurerDate,
                        serviceDate: this.$route.params.serviceDate,
                        disease: this.$route.params.disease,
                        sufferTiming: this.$route.params.sufferTiming,
                        notInformedReason: this.$route.params.notInformedReason,
                        settlementReason: this.$route.params.settlementReason,
                        settlementDate: this.$route.params.settlementDate,
                        insurerKnow: '否'
                    }
                });
            }
        }  
    },
    components: {
        component1
    }
};
</script>