<template>
  <br>
  <div>
    <h5>保險公司何時得知此事 ?</h5>
    <div class="input-group mb-3">
        <input type="date" v-model="date" class="form-control">
        <div class="input-group-append">
            <button @click="press(1)" class="btn btn-outline-success" type="button">
                <b>確認</b>
            </button>
        </div>
    </div>
    <br>
    <button @click="press(0)" class="btn btn-outline-dark" style="width:100%; padding: 10px 30px">
        <b>不知道</b>
    </button>
  </div>
</template>

<script>
export default {
  name: "15-1",
  data (){
    return {
      date: null
    }
  },
  methods: {
    press: function(input){
      if(input == 1 && this.date == null){
          alert("請選擇日期");
      }
      else{
        var today = new Date();
        today.setHours(0,0,0,0);
        var date = new Date(this.date);
        date.setHours(0,0,0,0);
        var oneM = new Date(new Date(date).setMonth(date.getMonth()+1));
        if(today >= oneM){
          // alert("超過一個月");
          this.$router.push({name: 'result3'});
        }
        else{
          this.$router.push({ 
            name: '16',
            params:{
                name: this.$route.params.name,
                type: this.$route.params.type,
                samePeople: this.$route.params.samePeople,
                insurerCareer: this.$route.params.insurerCareer,
                insuredCareer: this.$route.params.insuredCareer,
                insurerDate: this.$route.params.insurerDate,
                serviceDate: this.$route.params.serviceDate,
                disease: this.$route.params.disease,
                nonConformingDisease: this.$route.params.nonConformingDisease,
                sufferTiming: this.$route.params.sufferTiming,
                notInformedReason: this.$route.params.notInformedReason,
                settlementReason: this.$route.params.settlementReason,
                settlementDate: this.$route.params.settlementDate,
                insurerKnow: "是"
            }
          });
        }
      }
    }
  }
};
</script>

